<script setup>
import { ref } from "vue";

const cities = ref([]);

fetch(window.APP_URL + "/cities.json")
  .then((res) => res.json())
  .then((data) => {
    data.forEach((el) => {
      cities.value.push(el);
    });
  });
</script>

<template>
  <main>
    <div class="container-fluid">
      <div class="row header">
        <div class="container">
          <h1 class="mb-4">Select a City or Town</h1>
          <p>
            Click on a city or town below to be redirected to a page specific to
            that location. This will show you every contest available to be
            voted on within the chosen location. Precinct counts for local
            contests are shown as city/town total.
          </p>
        </div>
      </div>
      <div class="row">
        <div class="container">
          <ul class="card-columns">
            <li class="city-select" v-for="(city, idx) in cities" :key="idx">
              <router-link :to="city.city_url" aria-current="page">{{
                city.city_name
              }}</router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </main>
</template>

<style sscoped>
a {
  text-decoration: none;
}

.city-select a:hover {
  text-decoration: underline;
}

.city-select {
  font-size: 1.8rem;
}

.card-columns {
  list-style: none;
  padding-inline-start: 0;
}

@media (min-width: 576px) {
  .card-columns {
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
    -webkit-column-gap: 1.25rem;
    -moz-column-gap: 1.25rem;
    column-gap: 1.25rem;
    orphans: 1;
    widows: 1;
  }
}

@media (max-width: 990px) {
  .city-select {
    font-size: 1.3rem;
  }

  .card-columns {
    column-count: 2;
  }
}
</style>